import TagManager from "react-gtm-module";

export const pushDataLayer = (title, arg, event?) => {
  const tagManagerArgs = () => {
    if (event) {
      return {
        dataLayer: {
          event,
          eventModel: {
            [title]: arg,
          },
        },
      };
    } else {
      return {
        dataLayer: {
          [title]: arg,
        },
      };
    }
  };
  TagManager.dataLayer(tagManagerArgs());
};
